"use client";

import { Photo } from "@/lib/types";
import PhotoCard from "./PhotoCard";

interface TimelineProps {
  photos: Photo[];
  onDelete: (id: string) => void;
}

export default function Timeline({ photos, onDelete }: TimelineProps) {
  if (photos.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center py-20 px-4 animate-fadeInUp">
        <div className="text-6xl mb-4 animate-heartbeat">&#x2665;</div>
        <h2 className="text-xl font-semibold text-stone-600 mb-2">
          No memories yet
        </h2>
        <p className="text-stone-400 text-center max-w-xs text-sm leading-relaxed">
          Start adding your favorite moments together. Every photo tells a part
          of your story.
        </p>
      </div>
    );
  }

  // Group photos by month/year
  const grouped = photos.reduce<Record<string, Photo[]>>((acc, photo) => {
    const date = new Date(photo.date + "T00:00:00");
    const key = date.toLocaleDateString("en-US", {
      month: "long",
      year: "numeric",
    });
    if (!acc[key]) acc[key] = [];
    acc[key].push(photo);
    return acc;
  }, {});

  return (
    <div className="relative max-w-6xl mx-auto px-4 pb-32">
      {Object.entries(grouped).map(([monthYear, monthPhotos], groupIndex) => (
        <div key={monthYear} className="mb-12">
          {/* Month/Year Label */}
          <div
            className="flex items-center justify-center mb-8 animate-fadeInUp opacity-0"
            style={{
              animationDelay: `${groupIndex * 0.15}s`,
              animationFillMode: "forwards",
            }}
          >
            <div className="h-px flex-1 bg-gradient-to-r from-transparent to-rose-200" />
            <div className="px-5 py-2 glass rounded-full">
              <span className="text-sm font-medium text-rose-500 tracking-wide">
                {monthYear}
              </span>
            </div>
            <div className="h-px flex-1 bg-gradient-to-l from-transparent to-rose-200" />
          </div>

          {/* Photo Grid */}
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 md:gap-8">
            {monthPhotos.map((photo, index) => (
              <PhotoCard
                key={photo.id}
                photo={photo}
                index={groupIndex * 10 + index}
                onDelete={onDelete}
              />
            ))}
          </div>
        </div>
      ))}
    </div>
  );
}
