"use client";

import { useState, useEffect, useCallback } from "react";
import { Photo } from "@/lib/types";
import Header from "@/components/Header";
import Timeline from "@/components/Timeline";
import AddButton from "@/components/AddButton";
import AddMemoryModal from "@/components/AddMemoryModal";
import FloatingHearts from "@/components/FloatingHearts";

export default function Home() {
  const [photos, setPhotos] = useState<Photo[]>([]);
  const [loading, setLoading] = useState(true);
  const [modalOpen, setModalOpen] = useState(false);

  const fetchPhotos = useCallback(async () => {
    try {
      const res = await fetch("/api/photos");
      const data = await res.json();
      setPhotos(data.photos || []);
    } catch (error) {
      console.error("Failed to fetch photos:", error);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchPhotos();
  }, [fetchPhotos]);

  const handleUpload = async (file: File, caption: string, date: string) => {
    const formData = new FormData();
    formData.append("file", file);
    formData.append("caption", caption);
    formData.append("date", date);

    const res = await fetch("/api/upload", {
      method: "POST",
      body: formData,
    });

    if (!res.ok) {
      throw new Error("Upload failed");
    }

    const { data } = await res.json();
    setPhotos(data.photos);
  };

  const handleDelete = async (id: string) => {
    try {
      const res = await fetch("/api/photos", {
        method: "DELETE",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ id }),
      });

      if (res.ok) {
        const data = await res.json();
        setPhotos(data.photos);
      }
    } catch (error) {
      console.error("Failed to delete photo:", error);
    }
  };

  return (
    <main className="min-h-screen relative">
      <FloatingHearts />

      <Header photoCount={photos.length} />

      {loading ? (
        <div className="flex flex-col items-center justify-center py-20">
          <div className="relative">
            <div className="w-12 h-12 rounded-full border-2 border-rose-200 border-t-rose-500 animate-spin" />
          </div>
          <p className="text-rose-400 text-sm mt-4 animate-pulse">
            Loading memories...
          </p>
        </div>
      ) : (
        <Timeline photos={photos} onDelete={handleDelete} />
      )}

      <AddButton onClick={() => setModalOpen(true)} />

      <AddMemoryModal
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
        onUpload={handleUpload}
      />
    </main>
  );
}
